.notes-container {
  width: 80%;

  h1 {
    font-family: $font-raleway;
    //text-transform: uppercase;
    margin-top: 20px;
    display: flex;
    justify-content: center;
    font-size: 30px;
    font-weight: 400;
    //color: ;
  }

  .notes-all {
    border: 1px solid #78d1e2;
    border-radius: 15px;
    padding: 10px;
    margin-top: 20px;
    margin-bottom: 20px;
    box-shadow: 0 0 10px #cfd6ec;
    background-color: white;
    &:hover{
      box-shadow: 1px 2px 15px #abb1bf;
    }
  }

  .notes-time {
    color: #9aa0ac;
    font-family: $font-Lato;
  }

  .notes-description {
    color: #a0556d;
    font-family: $font-raleway;
    margin-top: 5px;
    margin-bottom: 10px;
  }

  .notes-content {
    color: #3d3f44;
    font-family: $font-open-sans;
  }

  .notes-btn {
    background-color: #97b3ed;
    border: 1px solid #97b3ed;
    //border: none;
    border-radius: 15px;
    padding: 5px 15px;
    margin-top: 10px;
    color: white;
    transition: 0.5s;
    &:hover{
      background-color: #6c92ef;
    }
  }
}